











import { Component, Prop, Vue, Emit } from "vue-property-decorator";
@Component({
})

export default class Checkbox extends Vue {
  @Prop() name!: string;
  @Prop() value!: boolean;
  @Prop() label!: string;

  @Emit('update:value')
  updateValue(data: boolean) {
    return data;
  }

  get _value() {
    return this.value;
  }
  set _value(data) {
    this.updateValue(data);
  }
}
